import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/boats';

export const apiBoatGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiBoatPost = async (serviceUrl, boat) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: boat ? JSON.stringify(boat) : null,
  };
  return request(url, options);
};

export const apiBoatPut = async (serviceUrl, boat) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: boat ? JSON.stringify(boat) : null,
  };
  return request(url, options);
};

export const apiBoatDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
